.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 35%;
		@include max-md {
			width: 48%;
		}
		img {
			transform-origin: 20% 80%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		transform: scaleX(-1);
		width: 35%;
		@include max-md {
			width: 48%;
		}
		img {
			transform-origin: 20% 80%;
		}
	}
	.background-element {
		&::before {
			position: absolute;
			width: 100%;
			bottom: 0;
			left: 0;
			height: 30%;
			background: linear-gradient(to top, var(--color-main) 20%, transparent 100%);
			content: "";
		}
	}
	.image-wrapper {
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 80%;
		&::after {
			position: absolute;
			inset: 0;
			content: "";
			background-color: rgba($color: #000000, $alpha: 0.15);
		}
		.venue-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			object-position: center center;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			padding: 3rem 2rem;
			background-color: var(--color-primary);
			border-radius: 20px;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
		.venue-content {
			position: relative;
			text-align: center;
			.card {
				border: none;
				background-color: var(--color-primary);
				height: 100%;
				border-radius: 16px;
				.card-body {
					padding: 0 2rem 3rem;
					@include max-md {
						padding: 0 1rem 2rem;
					}
					h4 {
						font-weight: 600;
						margin-bottom: 2rem;
					}
					.ribbon-venue {
						position: relative;
						width: fit-content;
						padding: 1rem;
						background-color: var(--color-primary);
						border-radius: 10px;
						transform: translateY(-50%);
						margin: 0 auto;
						border-radius: 0;
						border-radius: 1000px;
						svg {
							width: 40px;
							height: auto;
							path {
								stroke: var(--text-button);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin-top: 3rem;
		position: relative;
		padding: 2rem;
		border-radius: 10px;
		background-color: var(--color-primary);
		color: var(--color-dark);
		text-align: center;
		z-index: 2;
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
		}
	}
	.row-venue {
		gap: 3rem;
		&-sm {
			@include max-md {
				gap: 3rem;
			}
		}
	}
	.event-place {
		h4 {
			width: fit-content;
			margin: 0 auto 1rem;
			transform-origin: 100% 100%;
		}
	}
	.event-name {
		position: relative;
		margin-bottom: 3rem;
		h4 {
			width: fit-content;
			margin: 0 auto 1rem;
			transform-origin: 0% 100%;
		}
		.date {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 0.5rem;
			text-align: left;
			h5 {
				font-weight: 700;
				margin-bottom: 0;
				font-size: 38px;
			}
			p {
				margin-top: 0.2rem;
				font-size: 12px;
				font-weight: 400;
			}
			div {
				background-color: var(--color-dark);
				width: 2px;
				height: 38px;
				border-radius: 20px;
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.btn-custom {
		background-color: transparent;
		border-color: var(--color-button-trans);
		color: var(--color-button-trans);
		transition: all 0.25 ease-in-out;
		&:hover {
			background-color: var(--color-secondary) !important;
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
}
