.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	&::before {
		position: absolute;
		top: 0;
		left: 0;
		content: "";
		width: 100%;
		height: 100%;
		background-image: url(../images/bg-brush-cover.png);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&.orn-3 {
			transform: translateY(-32%);
			@include max-xl {
				transform: translateY(-25%);
			}
			@include max-lg {
				transform: translateY(-10%);
			}
			@include max-md {
				transform: translateY(-5%);
			}
			img {
				transform-origin: 30% 70%;
			}
		}
		.ornament-element {
			display: block;
			.ornament-wrapper-3 {
				position: absolute;
				top: 0;
				left: 0;
				width: 40%;
				transform: translateX(-30%);
				&:nth-child(2) {
					right: 0;
					left: unset;
					transform: scaleX(-1) translateX(-30%);
				}
			}
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-md {
			padding: 4rem 0 5rem;
			align-items: center;
		}
		@include max-cwh(425px, 586px) {
			padding: 3.5rem 0 5rem;
			align-items: flex-start;
		}
		.cover-content {
			text-align: center;
			.cover-body {
				position: relative;
				background-color: transparent;
				border-radius: 20px;
				color: var(--color-dark);
				&::before {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
					background-color: var(--color-primary);
					opacity: 0.7;
					content: "";
					border-radius: 20px;
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					aspect-ratio: 6/4;
					border-radius: 20px;
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 20px;
					}
				}
				.cover-description {
					padding: 1.5rem 1.5rem 0;
					text-align: center;
					[data-anim="rotate-right"] {
						width: fit-content;
						margin: 0 auto;
						transform-origin: 0% 100%;
					}
					[data-anim="rotate-left"] {
						width: fit-content;
						margin: 0 auto;
						transform-origin: 100% 100%;
					}
				}
				.elements-widget {
					transform: translateY(50%);
					width: 100%;
				}
			}
		}
	}
}
