.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	margin-top: 18rem;
	@include max-lg {
		margin-top: 0;
	}
	@include max-md {
		padding: 5rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		transform: scaleX(-1) translateX(35%);
		width: 180px;
		@include max-lg {
			width: 160px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		transform: translateX(35%);
		width: 160px;
		@include max-lg {
			width: 140px;
		}
		@include max-xs {
			width: 120px;
		}
		img {
			transform-origin: 25% 100%;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 220px;
		z-index: 5;
		@include max-xl {
			width: 180px;
		}
		@include max-lg {
			width: 160px;
		}
		@include max-md {
			width: 140px;
			bottom: 15rem;
		}
		@include max-xs {
			width: 120px;
		}
	}
	.orn-4 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 240px;
		transform: translate(30%, 10%) rotate(5deg) scaleX(-1);
		@include max-lg {
			width: 200px;
		}
		@include max-md {
			transform: translateX(30%, 0) rotate(5deg) scaleX(-1);
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, 0);
		}
		border-radius: 50px;
		font-weight: 600;
		background: var(--color-gradient) !important;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 50px;
		aspect-ratio: 1;
		position: relative;
		z-index: 3;
		@include max-md {
			margin: 1rem auto;
		}
		&::before {
			position: absolute;
			top: 3px;
			left: 3px;
			right: 3px;
			bottom: 3px;
			content: "";
			background-color: var(--color-main);
			border-radius: 50px;
		}
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 0 2rem 2rem;
		border-radius: 20px;
		margin-top: 10rem;
		@include max-md {
			padding: 0 1rem 2rem;
		}
		.background-full {
			top: unset;
			height: calc(100% - 10rem);
			&.man {
				transform-origin: 100% 100%;
			}
			&.woman {
				transform-origin: 0% 100%;
			}
		}
		.image-wrap {
			width: 80%;
			margin: -10rem auto 1.25rem;
			position: relative;
			aspect-ratio: 3/4;
			border-radius: 1000px;
			background: var(--color-gradient);
			padding: 3px;
			@include max-xl {
				width: 90%;
			}
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: 1000px;
			}
		}
		.couple-parent {
			width: 100%;
			margin: auto;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: linear-gradient(var(--color-main), var(--color-main)) padding-box, var(--color-gradient) border-box;
				border: 1px solid transparent;
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background: var(--color-secondary);
					color: var(--text-button);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
