.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 8rem 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 35%;
		@include max-md {
			width: 48%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		transform: scaleX(-1);
		width: 35%;
		@include max-md {
			width: 48%;
		}
	}
	.stream-wrapper {
		position: relative;
		padding: 3rem 2rem;
		border-radius: 20px;
		background-color: var(--color-primary);
		z-index: 5;
		margin-top: -1rem;
		@include max-md {
			padding: 3rem 1rem 2rem;
		}
		h4 {
			@include max-md {
				text-align: center;
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.card {
		background-color: transparent;
		height: 100%;
		color: var(--color-dark);
		border: none;
	}
	.btn-custom {
		width: 50%;
		z-index: 10;
		position: relative;
		@include max-lg {
			width: 100%;
		}
		@include max-md {
			width: fit-content;
		}
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		width: 100%;
		margin-top: 3rem;
		@include max-md {
			text-align: center;
		}
	}
}
