.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.row {
			gap: 2rem;
		}
	}
	&::before {
		position: absolute;
		width: 100%;
		top: 0;
		left: 0;
		height: 30%;
		background: linear-gradient(to bottom, var(--color-main) 20%, transparent 100%);
		content: "";
	}
	.orn-1 {
		position: absolute;
		right: 0;
		bottom: 5rem;
		width: 200px;
		transform: translateX(10%) scaleX(-1);
		z-index: 5;
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			bottom: 50%;
			width: 160px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		height: auto;
		width: 80%;
		aspect-ratio: 3/4;
		margin: 0 auto;
		border-radius: 1000px;
		@include max-lg {
			width: 100%;
		}
		.rsvp-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 1000px;
		}
	}
	.rsvp-wrap {
		position: relative;
	}
}
