.wishes-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		left: 0;
		top: 10rem;
		width: 200px;
		transform: translateX(-30%) rotate(5deg);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 20rem;
			transform: translateX(-40%) rotate(5deg);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.background-element {
		&::before {
			position: absolute;
			width: 100%;
			bottom: 0;
			left: 0;
			height: 30%;
			background: linear-gradient(to top, var(--color-main) 20%, transparent 100%);
			content: "";
		}
	}
	.image-wrap {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.wishes-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.card-form {
		margin-bottom: 0;
		background-color: transparent;
		position: relative;
		z-index: 3;
		border-radius: 10px;
		&.card {
			border: none;
		}
		.card-body {
			padding: 0;
		}
	}

	.wishes-preview {
		position: relative;
		padding: 0 0 3rem;
		margin-top: 2rem;
		@include max-lg {
			padding: 0 0 5rem;
		}
		@include max-md {
			padding: 0 0 3rem;
		}
		.card {
			border: none;
			position: relative;
			background-color: transparent;
			.card-body {
				overflow: hidden;
				padding: 0;
				.wishes {
					text-align: left;
					height: 26.05rem;
					overflow: auto;
					padding-right: 0.5rem;
					&::-webkit-scrollbar {
						width: 5px;
					}
					&::-webkit-scrollbar-track {
						background-color: #eee;
						border-radius: 20px;
						opacity: 0.15;
					}
					&::-webkit-scrollbar-thumb {
						background-color: var(--color-secondary);
						border-radius: 20px;
					}
					// .text-muted {
					// 	color: rgba($color: #fff, $alpha: 0.8) !important;
					// }
					.wish {
						margin-bottom: 1.5rem;
						display: grid;
						grid-template-columns: auto 1fr;
						gap: 1rem;
						h6 {
							font-weight: bold;
						}
						h6,
						p {
							margin-bottom: 0;
						}
						.wish-badge {
							background-color: var(--color-primary);
							width: 35px;
							aspect-ratio: 1;
							display: flex;
							justify-content: center;
							align-items: center;
							border-radius: 50px;
						}
						.wish-description {
							padding: 1rem;
							background-color: var(--color-primary);
							border-radius: 5px;
							position: relative;
							&::before {
								position: absolute;
								top: 15px;
								left: 0;
								width: 15px;
								height: 15px;
								transform: translateX(-100%);
								clip-path: url(#wishes-polygon);
								content: "";
								background-color: var(--color-primary);
							}
						}
					}
				}
			}
		}
	}
}
