.story-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 200px;
		transform: translateX(20%);
		z-index: 5;
		@include max-lg {
			width: 160px;
		}
		@include max-xs {
			width: 140px;
		}
		img {
			transform-origin: 25% 100%;
		}
	}
	.story-description {
		position: relative;
		text-align: center;
		border-radius: 20px;
		padding: 3rem 5rem 3rem 2rem;
		background-color: var(--color-primary);
		transform: translateX(3rem);
		@include max-lg {
			padding: 12rem 2rem 2rem;
			margin-top: -10rem;
			transform: none;
		}
		@include max-md {
			padding: 12rem 1rem 2rem;
		}
		* {
			position: relative;
		}
	}
	.image-story-wrap {
		position: relative;
		width: 100%;
		aspect-ratio: 4/6;
		background: var(--color-gradient);
		padding: 3px;
		border-radius: 1000px;
		z-index: 3;
		.story-image {
			object-fit: cover;
			width: 100%;
			height: 100%;
			border-radius: 1000px;
		}
	}
}
