.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 0;
		transform: translateY(-50%);
		width: 240px;
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 140px;
			top: 45%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		width: 200px;
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 140px;
		}
	}
	.orn-3 {
		position: absolute;
		left: 0;
		bottom: 5rem;
		width: 200px;
		transform: translateX(-30%) rotate(5deg);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			top: 20rem;
			transform: translateX(-40%) rotate(5deg);
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.orn-4 {
		position: absolute;
		right: 0;
		bottom: 0;
		width: 260px;
		transform: translate(40%, 30%) rotate(5deg) scaleX(-1);
		@include max-lg {
			width: 220px;
		}
		@include max-md {
			top: 20rem;
			transform: translateX(40%, 30%) rotate(5deg) scaleX(-1);
			width: 180px;
		}
		img {
			transform-origin: 0% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		overflow: hidden;
		background: var(--color-gradient);
		padding: 3px;
		border-radius: 20px;
		@include max-md {
			aspect-ratio: 4/3;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 20px;
		}
	}
	.thank {
		position: relative;
		padding: 3rem 0 0;
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
