.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 0);
		@include max-lg {
			width: 180px;
		}
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 30% 100%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		&.town-hall {
			transform: translateY(20%);
			@include max-lg {
				transform: translateY(60%);
			}
			@include max-md {
				transform: translateY(70%);
			}
		}
		&.orn-2 {
			top: unset;
			bottom: 0;
			transform: translateY(65%);
			@include max-lg {
				transform: translateY(15%);
			}
			@include max-md {
				transform: translateY(5%);
			}
			img {
				transform-origin: 0% 90%;
			}
		}
		&.orn-3 {
			transform: translateY(-32%);
			@include max-xl {
				transform: translateY(-25%);
			}
			@include max-lg {
				transform: translateY(-10%);
			}
			@include max-md {
				transform: translateY(-5%);
			}
			img {
				transform-origin: 30% 70%;
			}
		}
		.background-element {
			display: block;
			.background-wrapper {
				width: 100%;
				display: inline-block;
				&.scaleY-reverse {
					transform: scale(-1);
				}
			}
		}
		.ornament-element {
			display: block;
			.ornament-wrapper {
				position: absolute;
				left: 0;
				bottom: 0;
				width: 35%;
				&:nth-child(2) {
					right: 0;
					left: unset;
					transform: scaleX(-1);
				}
			}
			.ornament-wrapper-3 {
				position: absolute;
				top: 0;
				left: 0;
				width: 40%;
				transform: translateX(-30%);
				&:nth-child(2) {
					right: 0;
					left: unset;
					transform: scaleX(-1) translateX(-30%);
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		overflow: hidden;
		margin-top: 1rem;
		border-radius: 20px;
		background: linear-gradient(90deg, #c6a058 0%, #ffda92 100%);
		padding: 3px;
		@include max-lg {
			aspect-ratio: 1;
		}
		.background-header {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 20px;
		}
	}
	.header-content {
		position: relative;
		text-align: center;
		padding: 5rem 0 0;
		@include max-lg {
			padding: 10rem 0 0;
		}
		@include max-md {
			padding: 4.5rem 0 0;
		}
		.header {
			text-align: center;
			width: 100%;
			height: 100%;
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
			align-items: center;
			.header-title {
				z-index: 5;
				width: 100%;
				.title {
					transform-origin: 100% 100%;
					width: fit-content;
					margin: 0 auto;
					animation-timing-function: ease-in-out;
				}
				.header-description {
					transform-origin: 0% 100%;
					width: fit-content;
					margin: 0 auto;
					animation-timing-function: ease-in-out;
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
